body {
  margin: 0;
  background: #e0dfde;
  font-family: SegoeUI-SemiBold-final, Segoe UI Semibold, sans-serif;
  height: 100%;
  min-width: 1000px;

  a {
    text-decoration: none;
    color: black;
  }

  header {
    display: flex;
    align-items: center;
    background: white;
    justify-content: space-between;
    height: 100px;
    padding: 0 20px;
    margin: 0px 20px 0px 20px;
    border-radius: 0px 0px 20px 20px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);

    .title {
      display: flex;
      align-items: center;
      color: black;

      img {
        margin-right: 10px;
      }
    }

    .controls {
      position: relative;
      display: flex;
      align-items: center;

      #cartImg {
        height: 70px;
        width: 70px;
        margin-right: 10px;
      }

      .cartControl {
        display: inline-block;
        position: relative;

        .quantityCircleBadge {
          position: absolute;
          top: 5px;
          right: 5px;
        }
        .sumRectangleBadge {
          position: absolute;
          top: 50px;
          left: -10px;
        }
      }
      .cartControl:hover {
        scale: 1.1;
      }

      .userBlock {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        #avatarImg {
          height: 80px;
          width: 80px;
        }
      }
      .userBlock:hover {
        scale: 1.1;
        cursor: pointer;
      }
    }

    .controls > * {
      margin-left: 10px;
    }

    .controls.hidden > * {
      display: none;
    }
  }

  main {
    min-height: calc(100vh - 240px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    padding: 20px;
    position: relative;

    .signInSection {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      margin-top: 50px;
      width: 400px;

      .avatarContainer {
        position: relative;
        width: 300px;
        height: 330px;

        img {
          height: 300px;
          margin-bottom: 10px;
        }

        .hairOverlay {
          position: absolute;
          top: 30px;
          left: 50px;
          scale: 1.3;
        }

        .switchContainer {
          position: absolute;
          top: -50px;
          right: -50px;
          z-index: 1;
        }
      }

      .disabledButtonStyle {
        cursor: not-allowed;
      }

      * {
        margin-bottom: 2px;
      }
    }

    .cartSection {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      .purchaseButton {
        position: absolute;
        top: 20px;
        right: 20px;
      }

      .backToBooksButton {
        position: absolute;
        top: 20px;
        left: 20px;
      }
      .cartTableContainer {
        margin-top: 60px;
        min-width: 800px;
        width: 800px;
        border-radius: 20px 5px 20px 5px;

        .cartBookImage {
          height: 100px;

          border-radius: 10px;
          border: 1px solid #e0dfde;
          box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
        }
        th {
          font-size: 16px;
          text-align: center;
          font-weight: bold;
        }

        td {
          font-size: 16px;
        }
      }
    }

    .errSection {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      margin-top: 50px;
      width: 400px;
      img {
        height: 300px;
        margin-bottom: 10px;
      }

      h1 {
        color: red;
      }

      * {
        margin-bottom: 2px;
      }
    }

    .bookListSection {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: flex-start;
      justify-content: center;
      max-width: 1360px;
      min-height: calc(100vh - 260px);
      width: 100%;
      height: 100%;

      .bookFilterSection {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 100%;
        margin-bottom: 10px;

        .selectPrice {
          width: 250px;
        }
        * {
          margin: 10px;
        }

        .filterIcon.hidden {
          display: none;
        }
      }

      .bookCard {
        border-radius: 20px 5px 20px 5px;
        margin-right: 20px;
        margin-bottom: 20px;
        width: 250px;
        height: 420px;
        .bookCardHeader {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          height: 360px;
          width: 100%;

          .bookCardImage {
            margin-bottom: 10px;
            height: 250px;
            width: 190px;
            box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
            border-radius: 10px;
            border: 1px solid #e0dfde;
          }
          h3 {
            margin-bottom: 2px;
            margin-top: 0;
            margin-left: 10px;
          }
          p {
            margin-top: 0;
            margin-bottom: 2px;
            margin-left: 10px;
          }
        }

        .bookCardFooter {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          width: 100%;
          height: 45px;

          * {
            margin: 20px;
          }
        }
      }
    }

    .specificBookSection {
      display: flex;
      position: relative;
      flex-direction: row;
      align-items: flex-start;
      justify-content: flex-start;
      max-width: 1500px;
      padding: 50px;
      border-radius: 20px 5px 20px 5px;

      .specificBookCard {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        max-width: 800px;

        .bookImg {
          height: 328px;
          width: 250px;
          border-radius: 10px;
          border: 1px solid #e0dfde;
          box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
        }

        .specificBookCardHeader {
          display: flex;
          height: 328px;
          flex-direction: column;
          justify-content: space-between;
          margin-left: 20px;
          * {
            margin-top: 0px;
            margin-bottom: 5px;
          }
        }
        .specificBookCardFooter {
          padding: 10px;
          margin: 10px 50px 0 0;
          text-align: justify;
          text-indent: 20px;
        }
      }

      .specificBookOrderDetails {
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        .specificBookOrderCard {
          width: 360px;
          height: 200px;
          padding: 20px;
          margin-bottom: 20px;
          border-radius: 20px 5px 20px 5px;

          .priceSection {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            height: 45px;
          }

          .quantitySection {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 100%;
          }

          .totalSumSection {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: 100%;
            height: 45px;
          }

          .changeButtonSection {
            display: flex;
            justify-content: space-between;
            width: 100%;
          }

          .addButtonSection {
            display: flex;
            justify-content: flex-end;
            width: 100%;
          }
        }

        .specificBookCartInfo {
          margin-top: 20px;
          width: 350px;
          height: 250px;
          padding: 20px;
          border-radius: 5px 20px 5px 20px;
        }
      }
    }
  }

  footer {
    display: flex;
    background: white;
    justify-content: center;
    align-items: center;
    height: 100px;
    width: calc(100% - 40px);
    margin: 0px 20px 0px 20px;
    border-radius: 20px 20px 0px 0px;
    box-shadow: 0px -4px 6px rgba(0, 0, 0, 0.1);
  }

  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    display: none;
  }
}

.dialogContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.dialogContent * {
  margin: 20px;
}
